                        class="rounded slide-hover full-width text-body1 font-medium button-gm-outline-dark button-lg"
                        @click="$root.goTo_Home"
                      >
                        <q-icon
                          size="20px"
                          name="icon-arrow-narrow-left"
                          class="q-pr-sm"
                        />
                        Back to home
                      </q-btn>
                    </div>
                    <div class="col">
                      <q-btn
                        no-caps
                        unelevated
                        :ripple="{ color: 'accent' }"
                        color="primary"
                        class="rounded slide-hover full-width text-body1 font-medium button-lg"
                        @click="goTo_Directory()"
                      >
                        Add your next request
                      </q-btn>
                    </div>
                  </template>
                </div>
              </q-form>
            </div>
          </q-card-section>
        </q-card>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import { defineComponent, ref, defineAsyncComponent } from 'vue';
import GrowmodoTextIcon from 'src/components/GrowmodoTextIcon.vue';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import {
  Editor_Mixin,
  FileUploader_Mixin,
  RequestOptions_Mixin,
} from 'src/mixins';
import { checkIfArray } from 'src/assets/scripts/functions';
import { mapState, mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useApp from 'stores/app';
import { extractErrorMessageString } from 'src/assets/scripts/functions.js';
import fnStore from 'stores/fn.store';

export default defineComponent({
  name: 'FormNewRequest',

  mixins: [Editor_Mixin, FileUploader_Mixin, RequestOptions_Mixin],

  components: {
    GrowmodoTextIcon,
    CardIconBox,
    SelectBrand: defineAsyncComponent(() =>
      import('src/components/Brands/SelectBrand.vue')
    ),
    SelectPlatforms: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectPlatforms.vue')
    ),
    RadioSelect: defineAsyncComponent(() =>
      import('src/components/Helpers/RadioSelect.vue')
    ),
    CheckboxSelect: defineAsyncComponent(() =>
      import('src/components/Helpers/CheckboxSelect.vue')
    ),
    InputTextField: defineAsyncComponent(() =>
      import('src/components/Helpers/InputTextField.vue')
    ),
    TextArea1: defineAsyncComponent(() =>
      import('src/components/Helpers/TextArea1.vue')
    ),
    TextArea2: defineAsyncComponent(() =>
      import('src/components/Helpers/TextArea2.vue')
    ),
    InputVideoWalkthrough: defineAsyncComponent(() =>
      import('src/components/Helpers/InputVideoWalkthrough.vue')
    ),
    SelectOption: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectOption.vue')
    ),
    InputWebsite: defineAsyncComponent(() =>
      import('src/components/Helpers/InputWebsite.vue')
    ),
    InputPhoneNumber: defineAsyncComponent(() =>
      import('src/components/Helpers/InputPhoneNumber.vue')
    ),
    FileUploader: defineAsyncComponent(() =>
      import('src/components/Helpers/FilesUploader.vue')
    ),
    SelectCategoryType: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectCategoryType.vue')
    ),
  },

  data() {
    return {
      step: -1,
      answer: ref({}),
      request_submitting: false,
      request_submitted: false,
      containerSideMenu: false,
      uploads: {},
    };
  },

  computed: {
    ...mapState(useGrowmodo, [
      'taskDirectory_Categories',
      'projectDirectory_Categories',
      'taskDirectory',
      'projectDirectory',
    ]),

    taskRouteId() {
      return this.$route.params?.taskId;
    },

    projectRouteId() {
      return this.$route.params?.projectId;
    },

    isTask() {
      return this.taskRouteId;
    },

    requestInfo() {
      return (
        (this.isTask
          ? this.taskDirectory[this.taskRouteId]
          : this.projectDirectory[this.projectRouteId]) || {}
      );
    },

    showSkeleton() {
      return (
        !this.dynamic_questions_array.length ||
        Object.keys(this.answer).length !==
          this.dynamic_questions_array.length ||
        this.step === -1
      );
    },

    dynamic_questions_array() {
      return /* this.testQuestions || */ this.requestInfo.questions || [];
    },

    dynamic_questions() {
      const data = {};
      for (const question of this.dynamic_questions_array) {
        data[question.id] = question;
      }
      return data;
    },
  },

  watch: {
    ['taskRouteId']: {
      async handler(val) {
        if (val) {
          await this.init_declare();
          this.init();
        }
      },
    },
  },

  async mounted() {
    this.isTask
      ? await this.getTaskDirectory()
      : await this.getProjectDirectory();
    await this.init_declare();
    this.init();
  },

  methods: {
    ...mapActions(useGrowmodo, [
      'requestTask',
      'requestProject',
      'getTaskDirectory',
      'getProjectDirectory',
    ]),
    ...mapActions(useApp, ['init_RequestForm']),

    async init_declare() {
      this.step = -1;
      this.answer = ref({});
      this.request_submitting = false;
      this.request_submitted = false;
      this.uploads = ref({});
      // Back to Home if Not Task or Poject
      if (!this.requestInfo?.title) this.$root.goTo_Home();
    },

    async init() {
      this.$root.setMeta({ title: this.requestInfo.title });
      this.step = 0;
      this.init_RequestForm();
      for (const q of this.dynamic_questions_array) {
        let ans = { value: '', alternative_answer: '' };
        // Answers
        switch (q.type) {
          case 'url':
          case 'phone':
          case 'textarea1':
          case 'textarea2':
          case 'textfield':
          case 'select_brand':
          case 'video_walkthrough':
          case 'upload_single':
          case 'select_tasktype':
            ans.value = '';
            break;

          case 'select_multiple':
          case 'checkbox':
          case 'upload_multiple':
            ans.value = [];
            break;

          case 'radio':
          case 'select_platform':
            ans.value = {};
            break;

          default:
            break;
        }
        // Alternative Answers
        switch (q.alternative_type) {
          case 'url':
          case 'phone':
          case 'textarea1':
          case 'textarea2':
          case 'textfield':
          case 'select_brand':
          case 'video_walkthrough':
          case 'upload_single':
            ans.alternative_answer = '';
            break;

          case 'select_multiple':
          case 'checkbox':
          case 'upload_multiple':
            ans.alternative_answer = [];
            break;

          case 'radio':
          case 'select_platform':
            val.alternative_answer = {};
            break;

          default:
            break;
        }
        if (
          this.step === 0 ||
          this.dynamic_questions_array[this.step]?.id !== q.id
        ) {
          this.answer[q.id] = ans;
        }
      }
    },

    async submitRequest() {
      if (!this.validateData()) return;
      this.request_submitting = true;
      const payload = {
        title: this.requestInfo.title,
        [`${this.isTask ? 'task' : 'project'}_type_id`]:
          this.requestInfo.tags[0],
      };
      const dynamic_questions = [];
      for (const i of Object.keys(this.answer)) {
        dynamic_questions.push({
          id: i,
          answer: checkIfArray(this.answer[i])
            ? this.answer[i]
            : [this.answer[i]],
        });
      }
      payload.dynamic_questions = dynamic_questions;
      const response = this.isTask
        ? await this.requestTask(payload)
        : await this.requestProject(payload);
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        if (message)
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: message,
          });
        this.step++;
        this.request_submitted = true;
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Request submittion failed!',
        });
      }
      this.request_submitting = false;
    },

    factoryFn(files) {
      return new Promise((resolve, reject) => {
        const token = fnStore.getAuth();
        resolve({
          method: 'POST',
          fieldName: 'uploads[]',
          url: `${this.$config?.api?.baseURL}/file/temp`,
          headers: [{ name: 'Authorization', value: `Bearer ${token}` }],
          withCredentials: false,
        });
      });
    },

    toogleContainerSideMenu(force = null) {
      this.containerSideMenu = force !== null ? force : !this.containerSideMenu;
    },

    validateData() {
      if (
        !this.answer ||
        isNaN(this.step) ||
        !this.dynamic_questions ||
        !this.dynamic_questions_array[this.step]
      )
        return;
      // Question Information
      const question = this.dynamic_questions_array[this.step] || {};
      // Upload needs to be done
      let response = true;
      if (question.type.startsWith('upload')) {
        const uploads = this.uploads[`upload${question.id}`];

        for (const file of uploads?.files || []) {
          if (file.__status === 'uploading') {
            response = false;
            break;
          } else {
            response = true;
          }
        }
      }
      if (!response) {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: 'Upload is still in progress.',
        });
        return false;
      } else {
        // No problem If not Required
        if (question.required === false) return true;
        // If Required and no answer
        if (!this.answer[question.id]?.value) return false;
        // Check if alternative and is provided
        if (
          question.alternative_if &&
          question.alternative_if == this.answer[question.id]?.value?.value &&
          !this.answer[question.id]?.value?.alternative_answer
        )
          return false;
        // If Some empty objects
        if (typeof this.answer[question.id]?.value === 'object') {
          if (
            JSON.stringify(this.answer[question.id]?.value) == '{}' ||
            JSON.stringify(this.answer[question.id]?.value) == '[]'
          )
            return false;
        }
        // If textarea2 can't just <br>
        if (
          question.type === 'textarea2' &&
          this.answer[question.id]?.value == '<br>'
        )
          return false;
        if (process.env.DEV_MODE)
          console.warn('Answer:', this.answer[question.id]);
        return true;
      }
    },

    previousStep() {
      this.$refs?.['formStepper']?.previous?.();
    },

    nextStep() {
      if (this.validateData()) this.$refs?.['formStepper']?.next?.();
    },

    gotoStep(step_no) {
      this.$refs?.['formStepper']?.goTo?.(step_no);
    },

    goTo_Directory(model) {
      if (!model) model = this.isTask ? 'task' : 'project';
      // Open Directory
      this.$root.toggleRequest(model == 'task' ? 'project' : 'task', false);
      this.$root.toggleRequest(model, true);
    },
  },
});
</script>
